* {
    font-family: "Poppins", sans-serif;
}

.container, .cards {
    display: flex;
}

.container {
    flex-direction: column;
}

.container {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    /* border: 1px solid black; */
    box-shadow: 5px 10px 20px #888888;
}

.header {
    background-color: rgb(228, 228, 228);
}

.header h1{
    margin-left: 20px;
}

.card {
    width: 11rem;
    height: 4rem;
    background-color: rgb(228, 228, 228);
    margin: 10px;
    text-decoration: none;
    color: black;
    text-align: center;
    padding: 40px 0 0 0;
    border-radius: 10px;

}

.card:hover {
    background-color:rgb(158, 156, 156)
}

.cards {
    margin-top: 20px;
    flex-wrap: wrap;
    
}

.new-blog {
    margin-left: 90%;
}

.new-blog img {
    width: 28px;
    /* border: 2px solid black; */
    border-radius: 10px;;
    padding: 5px;
    background-color: rgb(228, 228, 228);
    
}