* {
    font-family: "Poppins", sans-serif;
}

form, .text, .buttons {
    display: flex;
}

.text, .buttons {
    flex-direction: column;
}

.text input {
    border: none;
    outline: none;
    font-weight: 500;
    font-size: larger;
    text-align: center;
    width: 200%;
    margin-top: 20px;
}

.text textarea {
    margin-top: 20px;
    outline: none;
    border: none;
    width: 200%;
    margin-top: 20px;
    font-weight: 500;
    font-size: larger;
    text-align: center;
    resize: none;

}

.home-image {
    width: 24px;
    border-radius: 10px;;
    padding: 5px;
    background-color: rgb(228, 228, 228);
    margin-bottom: 10px;
    margin-top: 10px;
}

.buttons input {
    width: 24px;
    border: none;
    background-color: rgb(228, 228, 228);
    padding: 5px;
    border-radius: 5px;
}

.buttons, .text {
    margin-left: 20px;
}