* {
    font-family: "Poppins", sans-serif;
}

.secondary {
    display: flex;
}

.main {
    display: flex;
    flex-direction: column;
    word-break: break-all;
    text-align: center;
    margin-left: 20px;
    margin-right: 20px;
    flex: 1;
}

/* .main p {
    word-wrap: break-word;
